import { h } from './element';
import { cssPrefix } from '../config';
import { bindClickoutside, unbindClickoutside } from './event';


export default class Postil {
  /**
   * 
   * @param { Function } viewFn 视图函数
   * @param { Object } data 数据对象
   * @param { Function } refresh 刷新 canvas 函数
   */
  constructor(viewFn, data, refresh) {
    this.createPostil();
    this.hide();
    this.data = data;
    this.refresh = refresh;
    this.viewFn = viewFn();
    this.isShow = false;
    this.isInputting = false; // 输入时，移动鼠标不将其隐藏
  }

  // 隐藏
  hide() {
    if (!this.isShow) return;
    const { el } = this;
    el.hide();
    this.isShow = false; // 设置显示
    this.isInputting = false;
    this.multiInput.val(''); // 清空多行文本输入框
    unbindClickoutside(el);
  }

  // 创建批注的 DOM 节点
  createPostil() {
    this.el = h('div', `${cssPrefix}-postil`).children(
      h('i', 'postil-lead-line'),
      this.multiInput = h('textarea', 'postil-textarea')
    );
  }

  // 设置批注显示的位置
  setPosition(x, y, originRCVal, editable = false) {
    const { el, multiInput, data: { rows } } = this;

    const { vri: ri, vci: ci } = originRCVal;
    let postilContent = ''; // 初始化批注
    const cell = rows.getCell(ri, ci);
    if (cell) {
      postilContent = cell.annotation || ''; // 原单元格保留的批注
    }

    // 没值，设置原始尺寸，有值则设置尺寸（无论对应的 textarea 尺寸是否改变过）
    if(postilContent.trim() === '') {
      multiInput.css({ width: '200px', height: '100px' });
    } else {
      const { annotationTextareaSize: { width, height } } = cell;
      multiInput.css({ width: `${width}px`, height: `${height}px` });
    }
  
    this.isShow = true;
    multiInput.val(postilContent);
    el.offset({ left: x, top: y }).show();
    if(editable) { // 可编辑
      this.multiInput.removeAttr('readonly');
      this.multiInput.focus();
      this.isInputting = true;
    } else { // 不可编辑：只读，可拉伸但不保存尺寸了
      this.multiInput.attr('readonly', true);
    }

    // 绑定外部的点击事件
    bindClickoutside(el, el => {
      const newContent = multiInput.val(); // 获取多行文本框 textarea 的最新内容
      const { data: { rows } } = this;
      const cell = rows.getCellOrNew(ri, ci);
      cell.annotation = newContent;
      const { width, height } = multiInput.offset();
      cell.annotationTextareaSize = { width, height }; // 保存对应单元格的批注框尺寸
      this.hide(); // 隐藏
      this.refresh();
      unbindClickoutside(el);
    });
  }

}
