/* global window, document */
import { h } from './component/element';
import DataProxy from './core/data_proxy';
import Sheet from './component/sheet';
import { cssPrefix } from './config';
import { locale } from './locale/locale';
import { expr2xy } from './core/alphabet';
import { sheetReset } from './component/sheet';
// import { laydate } from './third-plugins/layDate-v5.0.9/laydate/laydate';
import './index.less';
import { DATASET } from './utils/constants';

class Spreadsheet {
  constructor(selectors, options = {}) {
    let targetEl = selectors;
    if (typeof selectors === 'string') {
      targetEl = document.querySelector(selectors);
    }
    this.data = new DataProxy(options);
    const rootEl = h('div', `${cssPrefix}`)
      .on('contextmenu', evt => evt.preventDefault());
    // create canvas element
    this.sheet = new Sheet(rootEl, this.data, options.fxObj);
    targetEl.appendChild(rootEl.el);
    window.xsObj.instanceArray.push(this);
  }

  loadData(data) {
    this.sheet.loadData(data);
    return this;
  }

  getData() {
    return this.data.getData();
  }

  validate() {
    const { validations } = this.data;
    return validations.errors.size <= 0;
  }

  change(cb) {
    this.data.change = cb;
    return this;
  }

  static locale(lang, message) {
    locale(lang, message);
  }
}

const spreadsheet = (el, options = {}) => {
  if (window.xsObj) {
    const { instanceArray } = window.xsObj;
    if (instanceArray) {
      let isDuplicate = false;
      instanceArray.some((value) => {
        if (value.name === options.name) {
          isDuplicate = true;
          return true;
        }
        return false;
      });
      // 存在同名的sheet名称
      if (isDuplicate) {
        const { hooks } = options;
        if (hooks && hooks.tipMessage) {
          hooks.tipMessage('toast', `存在同名sheet:${options.name}`);
        }
        console.error(`存在同名sheet:${options.name}`);
        return false;
      }
    }
  }
  // 检查是否有跨表公式依赖当前实例
  const instance = new Spreadsheet(el, options);
  setTimeout(() => {
    if (window.xsObj.afterExternalInstanceFinishInitialization[options.name]) {
      window.xsObj.externalInstanceFinishingInitialization(options.name, instance);
    }
  }, 0);
  return instance;
};

if (window) {
  window.xsObj = window.xsObj || {
    instanceArray: [],
    afterExternalInstanceFinishInitialization: {}, // 用于等待其他实例初始化后才执行
    getInstanceByName: (name) => {
      const { instanceArray } = window.xsObj;
      let instance;
      if (instanceArray) {
        instanceArray.some((value) => {
          if (value.data.name === name) {
            instance = value;
            return true;
          }
          return false;
        });
      }
      return instance;
    },
    // 用于等待其他实例初始化后才执行
    addExternalInstanceFinishInitialization(sheetName, callback) {
      if (!window.xsObj.afterExternalInstanceFinishInitialization[sheetName]) {
        window.xsObj.afterExternalInstanceFinishInitialization[sheetName] = [];
      }
      window.xsObj.afterExternalInstanceFinishInitialization[sheetName].push(callback);
    },
    externalInstanceFinishingInitialization(sheetName, externalInstance) {
      if (window.xsObj.afterExternalInstanceFinishInitialization[sheetName]) {
        window.xsObj.afterExternalInstanceFinishInitialization[sheetName].map((callback) => {
          callback(externalInstance.data, true);
          return true;
        });
        delete window.xsObj.afterExternalInstanceFinishInitialization[sheetName];
        // 触发依赖计算公式
        // setTimeout(() => {
        //   // linjian 更新计算依赖
        //   window.xsObj.instanceArray.map((instance) => {
        //     instance.data.dependences.all.forEach((dependence) => {
        //       const dependentNeedChanging = [];
        //       changedCells.map((changedCell) => {
        //         const copyCell = Object.assign({ sheetName: this.instance.name }, changedCell);
        //         const reference = dependence.getReference(copyCell);
        //         if (reference) {
        //           // 获取 A2 改变后的会影响到的所有单元格，添加的 dependentNeedChanging
        //           dependence.getAllDependentsByReference(reference, dependentNeedChanging, true);
        //         }
        //         return true;
        //       });
        //       dependence.afterReferenceChange(dependentNeedChanging, instance.sheet);
        //     });
        //     return true;
        //   });
        // }, 0);
      }
    },
    removeAll() {
      this.instanceArray.length = 0;
    },
    removeSheet(sheetName) {
      const { instanceArray } = window.xsObj;
      if (instanceArray) {
        instanceArray.some((value, index) => {
          if (value.data.name === sheetName) {
            instanceArray.splice(index, 1);
            return true;
          }
          return false;
        });
      }
      return instanceArray;
    },

    /**
     * 
     * @param { String } sheetName 表名称
     * @description 由名字获取到某张表的实例对象
     */
    _getSheetInst(sheetName) {
      const { instanceArray } = this;
      let currInstance = instanceArray[0]; // 默认为第一张表
      // 根据传入的表名改变当前数据对象
      for (let i = 0; i < instanceArray.length; i++) {
        const inst = instanceArray[i];
        const { data: { name } } = inst;
        if (name === sheetName) {
          currInstance = inst;
          break;
        }
      }
      // console.log('currInstance: ', currInstance);
      return currInstance;
    },

    /**
     * 
     * @param { String } sheetName 
     * @description 重置报表
     */
    _sheetReset(sheetName) {
      const { sheet } = this._getSheetInst(sheetName); // 当前实例的报表对象
      sheetReset.call(sheet);
    },

    /**
     * 
     * @param { String } sheetName 
     * @description 刷新报表
     */
    _refresh(sheetName) {
      const { sheet } = this._getSheetInst(sheetName); // 当前实例的报表对象
      sheet.refresh();
    },

    /**
     * 
     * @param { String } sheetName 表名称
     * @param { String } rc 行列值, 从 1 开始。 e.g. A2
     * @param { String } cellType 单元格主类型, e.g. DATASET
     * @param { String } type 单元格副类型，用做格式校验， e.g. 'currency'
     * @description 设置单元格的类型，副类型
     */
    _setCellType(
      { sheetName, rc, cellType, type }
      = { sheetName: 'sheet1', rc: 'A1', cellType: DATASET, type: null }
    ) {
      let cellIsNull = false; // cell 对象是否为 null
      const [real_ci, real_ri] = expr2xy(rc); // 行列值转成数值表示
      const { data, data: { rows } } = this._getSheetInst(sheetName); // 当前实例的数据对象
      let cell = data.getCell(real_ri, real_ci); // 获取到对应的单元格
      if (!cell) { // null
        cellIsNull = true;
        cell = {};
      }

      cell.cellProps = cell.cellProps || {}; // 获取单元格的属性对象
      const cps = cell.cellProps; // 保持引用不变
      type && (cps.type = type); // 直接赋值
      switch (cellType) { // 不同的类型，不同的处理
        case DATASET: // 数据集类型
          cell.readOnly = true; // 只读，不可编辑
          cps.cellType = cellType;
          break;
        default:
          cps.cellType = cellType;
          break;
      }

      if (cellIsNull) { // 为 null 直接赋值
        rows._[real_ri] = rows._[real_ri] || {}; // 保持引用不变
        const rowObj = rows._[real_ri]; // 保持引用不变
        rowObj.cells = rowObj.cells || {}; // 保持引用不变
        rowObj.cells[real_ci] = cell;
      }

      // const postCell = data.getCell(real_ri, real_ci);
      // console.log('postCell -> ', real_ri, real_ci, postCell);

    },

    /**
     * 
     * @param { String } sheetName 表名称
     * @param { Number } ri 行, 从 0 开始
     * @param { Number } ci 列, 从 0 开始
     * @param { String } text 输入文字
     * @description 设置单元格的文字内容
     */
    _setCellText(
      { sheetName, ri, ci, text }
      = { sheetName: 'sheet1', ri: 0, ci: 0, text: '' }) {
      const { data } = this._getSheetInst(sheetName); // 当前实例的数据对象
      data.setCellText(ri, ci, text, 'finished');
      this._refresh(sheetName);

      // const postCell = data.getCell(ri, ci);
      // console.log('_setCellText: ', ri, ci, text, postCell);

    }

  };
  window.xsObj.spreadsheet = spreadsheet;
  window.xsObj.spreadsheet.locale = (lang, message) => locale(lang, message);
}

export default Spreadsheet;
export {
  spreadsheet,
};
