import { h } from './element';
import { bindClickoutside, unbindClickoutside } from './event';
import { cssPrefix } from '../config';
import { tf } from '../locale/locale';

const isEqual  = require('lodash.isequal');
const cloneDeep = require('lodash.clonedeep');

const defaultMenuList = [
  { key: 'copy', title: 'contextmenu.copy', label: 'Ctrl+C' },
  { key: 'cut', title: 'contextmenu.cut', label: 'Ctrl+X' },
  { key: 'paste', title: 'contextmenu.paste', label: 'Ctrl+V' },
  { key: 'paste-value', title: 'contextmenu.pasteValue', label: 'Ctrl+Shift+V' },
  { key: 'paste-format', title: 'contextmenu.pasteFormat', label: 'Ctrl+Alt+V' },
  /* 
  { key: 'divider' },
  { key: 'insert-row', title: 'contextmenu.insertRow' },
  { key: 'insert-column', title: 'contextmenu.insertColumn' },
  { key: 'divider' },
  { key: 'delete-row', title: 'contextmenu.deleteRow' },
  { key: 'delete-column', title: 'contextmenu.deleteColumn' },
  { key: 'delete-cell-text', title: 'contextmenu.deleteCellText' },
  { key: 'divider' },
  { key: 'validation', title: 'contextmenu.validation' },
  { key: 'divider' },
  { key: 'cell-printable', title: 'contextmenu.cellprintable' },
  { key: 'cell-non-printable', title: 'contextmenu.cellnonprintable' },
  { key: 'divider' },
  { key: 'cell-editable', title: 'contextmenu.celleditable' },
  { key: 'cell-non-editable', title: 'contextmenu.cellnoneditable' }
   */
];

function buildMenuItem(item) {
  if (item.key === 'divider') {
    return h('div', `${cssPrefix}-item divider`);
  } else if (item.key === 'paste-options') { // 粘贴选项
    const pasteEls = [];
    item.subResource.forEach(v => {
      pasteEls.push(
        h('span', `${cssPrefix}-item paste-common ${v.key}`)
        .on('click', e => {
          if (typeof v.callback === 'function') {
            this.itemClick(v.key, e, v.callback);
          } else {
            this.itemClick(v.key, e);
          }
          this.hide();
        })
      );
    });

    const pasteTitle = h('div', `${cssPrefix}-item paste-options-title`).children(item.title());
    const pasteCluster = h('div', 'paste-options-cluster').children(...pasteEls);

    return (
      h('div')
      .children(
        pasteTitle,
        pasteCluster
      )
    );
  };

  return (
    h('div', `${cssPrefix}-item lop-menu-item ${item.key}`)
    .on('click', e => {
      if (typeof item.callback === 'function') {
        this.itemClick(item.key, e, item.callback);
      } else {
        this.itemClick(item.key, e);
      }
      this.hide();
    })
    .children(
      item.title(),
      h('div', `${cssPrefix}-label`).child(item.label || '')
    )
  );
}

function buildMenu(menuItems) {
  return menuItems.map(it => buildMenuItem.call(this, it));
}


// 类：右键菜单
export default class ContextMenu {
  constructor(viewFn, isHide = false, customMenuItems) {
    this.setContextMenuPanel(customMenuItems);
    this.el.hide();
    this.viewFn = viewFn;
    this.itemClick = () => {};
    this.isHide = isHide;
    this.position = {};
    this.prevContextMenuData = customMenuItems; // 旧数据
  }

  // 设置右键菜单的面板内容，自定义还是默认
  setContextMenuPanel(customMenuList) {
    // 没传入就用默认的
    const renderData = (customMenuList && customMenuList.length) ? customMenuList : defaultMenuList;
    if (isEqual(this.prevContextMenuData, renderData)) return;
    // 保存旧数据，如果当前展示的面板内容跟上一次一样，则不需要再做右键的清空和重排操作
    this.prevContextMenuData = renderData;
    const ectype = cloneDeep(renderData); // 副本

    const refineMenuList = ectype.map(v => {
      v.title && (v.title = tf(v.title)); // 翻译
      if (v.subResource && v.subResource.length) {
        v.subResource.forEach(sv => { // 粘贴选项
          sv.title && (sv.title = tf(sv.title));
        });
      }
      return v;
    });

    if (!this.el) { // 初始化时
      this.el = h('div', `${cssPrefix}-contextmenu`);
      this.el.children(...buildMenu.call(this, refineMenuList));
    } else { // 根据条件显示不同的面板内容
      this.el.removeChildren(); // 清空面板
      this.el.children(...buildMenu.call(this, refineMenuList)); // 重建右键菜单内容
    }
  }

  hide() {
    const { el } = this;
    el.hide();
    unbindClickoutside(el);
  }

  setPosition(x, y) {
    if (this.isHide) return;
    const { el } = this;
    const { height, width } = el.show().offset();
    const view = this.viewFn();
    let top = y;
    let left = x;
    this.position = { x, y }; // 保存当前的右键菜单的位置
    if (view.height - y <= height) {
      top -= height;
    }
    if (view.width - x <= width) {
      left -= width;
    }
    el.offset({ left, top });
    bindClickoutside(el);
  }
}
